import React, { useEffect, useState } from "react";
import { Buffer } from "buffer";
import Layout from "../Layout";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faHeart } from "@fortawesome/free-solid-svg-icons";
import { faHeart as strokeHeart } from "@fortawesome/free-regular-svg-icons";
import Typography from "@mui/material/Typography";
import Rating from "@mui/material/Rating";
import { useParams } from "react-router-dom";
import {
    getEntertainerById,
    getReviewById,
    latestOrderByUser,
    createCart,
    addPhotosById,
    deletePhotoById,
    getWishlistById,
    createWishlist,
    deleteWishlist,
} from "../../API/utilities";
import { userInfo } from "../../API/auth";
import "./EntertainerDetails.css";

export default function EntertainerDetails(props) {
    const [values, setValues] = useState({
        id: "",
        name: "",
        username: "",
        religion: "",
        tribe: "",
        boobs: "",
        category: "",
        role: [],
        profile: {},
        photos: [],
        newPhoto: "",
    });

    const [message, setMessage] = useState("");
    const [latestOrder, setLatestOrder] = useState({});
    const [review, setReview] = useState(0);
    const [isWish, setIsWish] = useState("");

    const id = useParams().id;

    const cartHandler = () => {
        const cart = {
            product: values.id,
            user: userInfo().decoded.id,
            refModel: "entertainer",
        };
        createCart(cart, userInfo().token)
            .then((result) => setMessage("successfully added to cart!"))
            .catch((err) => setMessage("failed to added to cart!"));
    };

    const formHandler = (e) => {
        setValues({
            ...values,
            newPhoto: e.target.files[0],
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        const formData = new FormData(e.target);
        addPhotosById(id, formData, userInfo().token)
            .then((result) => {
                getEntertainerById(id, userInfo().token)
                    .then((result) => {
                        setValues({
                            ...values,
                            id: result.data._id,
                            name: result.data.name,
                            username: result.data.username,
                            religion: result.data.religion,
                            tribe: result.data.tribe,
                            boobs: result.data.boobs,
                            category: result.data.category.name,
                            role: result.data.role,
                            profile: result.data.profile,
                            photos: result.data.photos,
                        });
                    })
                    .catch((err) => console.log(err));
            })
            .catch((err) => setMessage("something went wrong!"));
    };

    const deleteHandler = (entertainerId, photoId) => {
        deletePhotoById(
            { entertainerId: entertainerId, photoId: photoId },
            userInfo().token
        )
            .then(() => {
                getEntertainerById(id, userInfo().token)
                    .then((result) => {
                        setValues({
                            ...values,
                            id: result.data._id,
                            name: result.data.name,
                            username: result.data.username,
                            religion: result.data.religion,
                            tribe: result.data.tribe,
                            boobs: result.data.boobs,
                            category: result.data.category.name,
                            role: result.data.role,
                            profile: result.data.profile,
                            photos: result.data.photos,
                        });
                    })
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    const wishListHandler = () => {
        if (!isWish) {
            createWishlist({ id: id }, userInfo().token)
                .then((result) => setIsWish(result.data._id))
                .catch((err) => console.log(err));
        } else {
            deleteWishlist(isWish, userInfo().token)
                .then((result) => setIsWish(""))
                .catch((err) => console.log(err));
        }
    };

    useEffect(() => {
        getEntertainerById(id, userInfo().token)
            .then((result) => {
                setValues({
                    ...values,
                    id: result.data._id,
                    name: result.data.name,
                    username: result.data.username,
                    religion: result.data.religion,
                    tribe: result.data.tribe,
                    boobs: result.data.boobs,
                    category: result.data.category.name,
                    role: result.data.role,
                    profile: result.data.profile,
                    photos: result.data.photos,
                });
            })
            .catch((err) => console.log(err));

        getReviewById(id, userInfo().token)
            .then((result) => setReview(result.data.review))
            .catch((err) => console.log(err));

        latestOrderByUser(id, userInfo().token)
            .then((result) => setLatestOrder(result.data))
            .catch((err) => console.log(err));

        getWishlistById(id, userInfo().token)
            .then((result) => {
                setIsWish(result.data._id);
            })
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title={values.name}>
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12 cover-photo">
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <div className="row">
                            <div className="col-md-6 col">
                                {values.profile.data && (
                                    <div className="profile-picture">
                                        <img
                                            src={`data:${
                                                values.profile.contentType
                                            };base64,${Buffer.from(
                                                values.profile.data
                                            ).toString("base64")}`}
                                            className="card-img-top"
                                            alt={values.name}
                                        />
                                    </div>
                                )}
                            </div>
                            <div className="col-md-6 col my-3">
                                <div style={{ marginTop: "7rem" }}>
                                    <h2 className="name">{values.name}</h2>
                                    <p>
                                        <span className="fw-bold">
                                            Username:
                                        </span>{" "}
                                        <a
                                            className="text-decoration-none fw-bold text-dark"
                                            rel="noreferrer"
                                            target="_blank"
                                            href={`https://www.instagram.com/${values.username}`}
                                        >
                                            {values.username}
                                        </a>{" "}
                                        <br />
                                        <span className="fw-bold">
                                            Religion:
                                        </span>{" "}
                                        {values.religion} <br />
                                        <span className="fw-bold">
                                            Tribe:
                                        </span>{" "}
                                        {values.tribe === true
                                            ? "Chakma"
                                            : "Bangali"}{" "}
                                        <br />
                                        <span className="fw-bold">
                                            Boobs Size:
                                        </span>{" "}
                                        {values.boobs} <br />
                                        <span className="fw-bold">
                                            Role:
                                        </span>{" "}
                                        {values.role.length > 0 &&
                                            values.role.map((item) => (
                                                <>
                                                    <span
                                                        key={Math.random()}
                                                        className="badge bg-primary"
                                                    >
                                                        {item}
                                                    </span>{" "}
                                                </>
                                            ))}
                                        <br />
                                        <div>
                                            <Typography
                                                className="fw-bold"
                                                component="legend"
                                            >
                                                {values.name}'s Rating:
                                            </Typography>
                                            <Rating
                                                name="read-only"
                                                value={review}
                                                readOnly
                                            />
                                        </div>
                                        <br />
                                        {values.category} service available.{" "}
                                    </p>
                                    {userInfo().decoded.role !== "admin" &&
                                        (latestOrder.reviewStatus === true ||
                                            latestOrder.reviewStatus ===
                                                undefined) && (
                                            <button
                                                onClick={cartHandler}
                                                className="btn btn-sm btn-primary"
                                            >
                                                Add to Cart
                                            </button>
                                        )}
                                    {userInfo().decoded.role !== "admin" && (
                                        <span
                                            className="btn btn-sm"
                                            onClick={wishListHandler}
                                        >
                                            <FontAwesomeIcon
                                                className="fa-2x text-danger"
                                                icon={
                                                    isWish
                                                        ? faHeart
                                                        : strokeHeart
                                                }
                                            />
                                        </span>
                                    )}
                                    {userInfo().decoded.role === "admin" && (
                                        <form
                                            className="my-2"
                                            onSubmit={submitHandler}
                                        >
                                            <div className="form-group mb-1">
                                                <label for="photo">
                                                    Add photos
                                                </label>
                                                <input
                                                    type="file"
                                                    className="form-control-file"
                                                    id="photo"
                                                    name="photos"
                                                    multiple="multiple"
                                                    onChange={formHandler}
                                                />
                                            </div>
                                            {values.newPhoto && (
                                                <button
                                                    type="submit"
                                                    className="btn btn-primary"
                                                >
                                                    Submit
                                                </button>
                                            )}
                                        </form>
                                    )}
                                </div>
                            </div>
                        </div>
                        <hr />
                        <div className="row justify-content-center">
                            {values.photos.map((photo) => (
                                <div
                                    key={photo._id}
                                    className="col-md-4 col-12 text-center"
                                >
                                    <img
                                        src={`data:${
                                            photo.contentType
                                        };base64,${Buffer.from(
                                            photo.data
                                        ).toString("base64")}`}
                                        className="img-fluid"
                                        alt={values.name}
                                    />
                                    {userInfo().decoded.role === "admin" && (
                                        <button
                                            type="button"
                                            className="btn btn-danger btn-sm m-1"
                                            onClick={() =>
                                                deleteHandler(id, photo._id)
                                            }
                                        >
                                            <FontAwesomeIcon icon={faTrash} />
                                        </button>
                                    )}
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
