import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import Layout from "../Layout";
import { getWishlistByUserId, deleteWishlist } from "../../API/utilities";
import { userInfo } from "../../API/auth";
import { URL } from "../../API/url";

export default function Wishlist(props) {
    const [wishlist, setWishlist] = useState([]);
    const [message, setMessage] = useState(false);

    const deleteHandler = (id) => {
        deleteWishlist(id, userInfo().token)
            .then((result) => {
                setMessage("Removed from wishlist.");
                getWishlistByUserId(userInfo().token)
                    .then((result) => setWishlist(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    useEffect(() => {
        getWishlistByUserId(userInfo().token)
            .then((result) => setWishlist(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Wishlist">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Your Favourite List</h1>
                    {message && (
                        <div className="alert alert-success" role="alert">
                            {message}
                        </div>
                    )}
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <div className="row">
                            <div className="col-12">
                                {wishlist.map((item) => (
                                    <div className="card my-1" key={item._id}>
                                        <div className="card-body d-flex flex-row">
                                            <div
                                                style={{
                                                    marginRight: "5px",
                                                    width: "150px",
                                                    height: "150px",
                                                    overflow: "hidden",
                                                }}
                                            >
                                                <img
                                                    className="img-fluid"
                                                    src={`${URL}/entertainer/getProfile/${item.product._id}`}
                                                    alt=""
                                                />
                                            </div>
                                            <div>
                                                <p>
                                                    <span className="fw-bold">
                                                        Entertainer Name:
                                                    </span>{" "}
                                                    <Link
                                                        className="text-decoration-none text-dark fw-bold"
                                                        to={`/entertainer/${item.product._id}`}
                                                    >
                                                        {item.product.name}
                                                    </Link>
                                                </p>
                                                <p>
                                                    <span className="fw-bold">
                                                        Entertainer Username:
                                                    </span>{" "}
                                                    {item.product.username}
                                                </p>
                                                <p>
                                                    <span className="fw-bold">
                                                        Boobs Size:
                                                    </span>{" "}
                                                    {item.product.boobs}
                                                </p>
                                                <p>
                                                    <span className="fw-bold">
                                                        Roles:
                                                    </span>{" "}
                                                    {item.product.role.map(
                                                        (item) => (
                                                            <>
                                                                <span
                                                                    key={Math.random()}
                                                                    className="badge bg-primary"
                                                                >
                                                                    {item}
                                                                </span>{" "}
                                                            </>
                                                        )
                                                    )}
                                                </p>
                                                <div className="text-end">
                                                    <span
                                                        className="btn btn-sm"
                                                        onClick={() =>
                                                            deleteHandler(
                                                                item._id
                                                            )
                                                        }
                                                    >
                                                        <FontAwesomeIcon
                                                            className="fa-2x text-danger"
                                                            icon={faHeart}
                                                        />
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
