import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Typography from "@mui/material/Typography";
import Rating from "@mui/material/Rating";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import Layout from "../Layout";
import {
    getOrderById,
    deleteOrder,
    updateReview,
    updateOrder,
} from "../../API/utilities";
import { userInfo } from "../../API/auth";

export default function Order(props) {
    const [order, setOrder] = useState([]);
    const [message, setMessage] = useState(false);

    const deleteHandler = (id) => {
        deleteOrder(id, userInfo().token)
            .then((result) => {
                setMessage("order deleted successfully!");
                getOrderById(userInfo().token)
                    .then((result) => setOrder(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => setMessage("product delete failed!"));
    };

    const reviewHandler = (e, productId, orderId) => {
        updateReview(
            { id: productId, review: e.target.value },
            userInfo().token
        )
            .then(() => {
                updateOrder(orderId, { reviewStatus: true }, userInfo().token)
                    .then(() => {
                        getOrderById(userInfo().token)
                            .then((result) => setOrder(result.data))
                            .catch((err) => console.log(err));
                    })
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    useEffect(() => {
        getOrderById(userInfo().token)
            .then((result) => setOrder(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Order">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>My Orders</h1>
                    {message && (
                        <div className="alert alert-success" role="alert">
                            {message}
                        </div>
                    )}
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {order.map((item) => (
                            <div className="card my-1" key={item._id}>
                                <div className="card-body">
                                    <p>
                                        <span className="fw-bold">
                                            Order ID:
                                        </span>{" "}
                                        {item._id}
                                    </p>
                                    <p>
                                        <span className="fw-bold">
                                            Customer Email:
                                        </span>{" "}
                                        {item.user.email}
                                    </p>
                                    <p>
                                        <span className="fw-bold">
                                            Entertainer Name:
                                        </span>{" "}
                                        <Link
                                            className="text-decoration-none text-dark fw-bold"
                                            to={`/${item.refModel}/${item.product._id}`}
                                        >
                                            {item.product.name}
                                        </Link>
                                    </p>
                                    <p>
                                        <span className="fw-bold">
                                            Order Time:
                                        </span>{" "}
                                        {item.time}
                                    </p>
                                    <p>
                                        <span className="fw-bold">
                                            Order Status:
                                        </span>{" "}
                                        <span className="bg-secondary p-3 text-light">
                                            {item.done === false
                                                ? "Pending"
                                                : "Done"}
                                        </span>
                                    </p>
                                    {item.done === true &&
                                        item.reviewStatus === false && (
                                            <div
                                                class="alert alert-primary text-dark"
                                                role="alert"
                                            >
                                                Please rate your experience with{" "}
                                                {item.product.name}
                                                <div>
                                                    <Typography
                                                        className="fw-bold"
                                                        component="legend"
                                                    >
                                                        Add your rating for{" "}
                                                        {item.product.name}
                                                    </Typography>
                                                    <Rating
                                                        name="simple-controlled"
                                                        onChange={(e) =>
                                                            reviewHandler(
                                                                e,
                                                                item.product
                                                                    ._id,
                                                                item._id
                                                            )
                                                        }
                                                    />
                                                </div>
                                            </div>
                                        )}
                                    {item.done === true &&
                                        item.reviewStatus === true && (
                                            <span>
                                                <button
                                                    type="button"
                                                    className="btn btn-danger btn-sm m-1"
                                                    onClick={() =>
                                                        deleteHandler(item._id)
                                                    }
                                                >
                                                    <FontAwesomeIcon
                                                        icon={faTrash}
                                                    />
                                                </button>
                                            </span>
                                        )}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </Layout>
    );
}
