import React, { useState } from "react";
import { Navigate } from "react-router-dom";
import { signIn, signUp, auth } from "../../API/auth";
import Layout from "../Layout";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function Login(props) {
    const [values, setValues] = useState({
        mode: "signIn",
        message: "",
        name: "",
        email: "",
        password: "",
        success: false,
    });
    const { mode, message, name, email, password, success } = values;

    const modeHandler = () => {
        setValues({
            ...values,
            mode: mode === "signIn" ? "signUp" : "signIn",
        });
    };

    const formHandler = (e) => {
        setValues({
            ...values,
            [e.target.name]: e.target.value,
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        if (mode === "signIn") {
            signIn({ email, password })
                .then((result) => {
                    setValues({
                        ...values,
                        success: true,
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    setValues({
                        ...values,
                        message: "invalid email or password!",
                    });
                });
        } else {
            signUp({
                name,
                email,
                password,
            })
                .then((result) => {
                    setValues({
                        ...values,
                        success: true,
                        name: "",
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    setValues({
                        ...values,
                        message: "invalid email or password!",
                    });
                });
        }
    };

    return (
        <Layout title="Login">
            {success && <Navigate replace to="/dashboard" />}
            <div className="row justify-content-center">
                <div className="col-md-6">
                    <div
                        style={{
                            position: "relative",
                            background: "#D9D9D9",
                        }}
                        className="card rounded-4 my-5"
                    >
                        <div
                            style={{
                                width: 80,
                                height: 80,
                                left: "44%",
                                top: -40,
                                position: "absolute",
                                borderRadius: 100,
                                overflow: "hidden",
                            }}
                            className="bg-light border border-dark border-2"
                        >
                            <FontAwesomeIcon
                                style={{ padding: "0.8rem 1.1rem" }}
                                className="fa-3x text-dark"
                                icon={faUser}
                                flip
                            />
                        </div>
                        <div className="card-body">
                            <h4 className="text-center pt-4">
                                {mode === "signIn" ? "Sign in" : "Sign up"}
                            </h4>
                            {message && (
                                <div
                                    className="alert alert-danger"
                                    role="alert"
                                >
                                    {message}
                                </div>
                            )}
                            <form onSubmit={submitHandler}>
                                {mode === "signUp" ? (
                                    <>
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                className="form-control mb-1"
                                                id="name"
                                                name="name"
                                                placeholder="Enter Your Name"
                                                value={name}
                                                onChange={formHandler}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                className="form-control mb-1"
                                                id="email"
                                                name="email"
                                                placeholder="Enter Your Email"
                                                value={email}
                                                onChange={formHandler}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <input
                                                type="password"
                                                className="form-control mb-1"
                                                id="password"
                                                name="password"
                                                placeholder="Password"
                                                value={password}
                                                onChange={formHandler}
                                            />
                                        </div>
                                    </>
                                ) : (
                                    <>
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                className="form-control mb-1"
                                                id="email"
                                                name="email"
                                                placeholder="Enter Your Email"
                                                value={email}
                                                onChange={formHandler}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <input
                                                type="password"
                                                className="form-control mb-1"
                                                id="password"
                                                name="password"
                                                placeholder="Password"
                                                value={password}
                                                onChange={formHandler}
                                            />
                                        </div>
                                    </>
                                )}

                                <div className="text-end">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"
                                    >
                                        {mode === "signIn"
                                            ? "Sign in"
                                            : "Sign Up"}
                                    </button>
                                </div>
                            </form>
                        </div>
                        <div className="card-footer">
                            <p className="text-center" onClick={modeHandler}>
                                {mode === "signIn"
                                    ? "Not registered?"
                                    : "Already have an account?"}{" "}
                                <span className="text-primary">
                                    {mode === "signIn"
                                        ? "Create an account"
                                        : "Sign in"}
                                </span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}

export default Login;
