import React, { Component } from "react";
import { Navigate } from "react-router-dom";
import Layout from "../Layout";
import "./Home.css";
import { auth, signIn, signUp } from "../../API/auth";
import { URL } from "../../API/url";
import { newArrival } from "../../API/utilities";
import {
    faCaretLeft,
    faCaretRight,
    faUser,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import LOGO from "../../assets/img/dudu-khabo-logo.png";

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            newCollection: [],
            currentSlide: 0,
            mode: "signIn",
            success: false,
            name: "",
            email: "",
            password: "",
        };
    }

    showPrev = () => {
        if (this.state.currentSlide === 0) {
            this.setState({
                ...this.state,
                currentSlide: this.state.newCollection.length - 1,
            });
        } else {
            this.setState({
                ...this.state,
                currentSlide: this.state.currentSlide - 1,
            });
        }
    };

    showNext = () => {
        if (this.state.currentSlide >= this.state.newCollection.length - 1) {
            this.setState({ ...this.state, currentSlide: 0 });
        } else {
            this.setState({
                ...this.state,
                currentSlide: this.state.currentSlide + 1,
            });
        }
    };

    modeHandler = () => {
        this.setState({
            ...this.state,
            mode: this.state.mode === "signIn" ? "signUp" : "signIn",
        });
    };

    handleForm = (e) => {
        this.setState({
            ...this.state,
            [e.target.name]: e.target.value,
        });
    };

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.mode === "signIn") {
            signIn({ email: this.state.email, password: this.state.password })
                .then((result) => {
                    this.setState({
                        ...this.state,
                        success: true,
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    this.setState({
                        ...this.state,
                        message: "invalid email or password!",
                    });
                });
        } else {
            signUp({
                name: this.state.name,
                email: this.state.email,
                password: this.state.password,
            })
                .then((result) => {
                    this.setState({
                        ...this.state,
                        success: true,
                        name: "",
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    this.setState({
                        ...this.state,
                        message: "invalid email or password!",
                    });
                });
        }
    };

    componentDidMount(props) {
        newArrival().then((result) =>
            this.setState({
                ...this.state,
                newCollection: result.data,
            })
        );
    }

    render() {
        return (
            <Layout title="DUDU Khabo">
                {this.state.success && <Navigate replace to="/dashboard" />}
                <div className="row justify-content-center cover-image py-md-4 py-5">
                    <div className="col-md-5 col-12 text-light">
                        <div>
                            <h3>Welcome To</h3>
                            <img
                                style={
                                    window.innerWidth < 576
                                        ? { height: "55px", width: "auto" }
                                        : { height: "85px", width: "auto" }
                                }
                                src={LOGO}
                                alt=""
                            />
                            <p>Enjoy Yourself with Our Collections</p>
                        </div>
                        <div className="d-flex justify-content-md-start justify-content-center">
                            <button className="btn" onClick={this.showPrev}>
                                <FontAwesomeIcon
                                    className="text-light fa-3x"
                                    icon={faCaretLeft}
                                />
                            </button>
                            {this.state.newCollection.length > 0 && (
                                <div
                                    style={{
                                        height: "300px",
                                        width: "350px",
                                        overflow: "hidden",
                                    }}
                                >
                                    <img
                                        className="img-fluid"
                                        src={`${URL}/entertainer/getProfile/${
                                            this.state.newCollection[
                                                this.state.currentSlide
                                            ]._id
                                        }`}
                                        alt=""
                                    />
                                </div>
                            )}
                            <button className="btn" onClick={this.showNext}>
                                <FontAwesomeIcon
                                    className="text-light fa-3x"
                                    icon={faCaretRight}
                                />
                            </button>
                        </div>
                    </div>
                    <div className="col-md-4 col-12 py-5">
                        <div
                            style={{
                                position: "relative",
                                background: "#D9D9D9",
                            }}
                            className="card rounded-4 my-5"
                        >
                            <div
                                style={{
                                    width: 80,
                                    height: 80,
                                    left: 180,
                                    top: -40,
                                    position: "absolute",
                                    borderRadius: 100,
                                    overflow: "hidden",
                                }}
                                className="bg-light border border-dark border-2"
                            >
                                <FontAwesomeIcon
                                    style={{ padding: "0.8rem 1.1rem" }}
                                    className="fa-3x text-dark"
                                    icon={faUser}
                                    flip
                                />
                            </div>
                            <div className="card-body">
                                <h4 className="text-center pt-4">
                                    {this.state.mode === "signIn"
                                        ? "Sign in"
                                        : "Sign up"}
                                </h4>
                                {this.state.message && (
                                    <div
                                        className="alert alert-danger"
                                        role="alert"
                                    >
                                        {this.state.message}
                                    </div>
                                )}
                                <form onSubmit={this.handleSubmit}>
                                    {this.state.mode === "signUp" ? (
                                        <>
                                            <div className="form-group">
                                                <input
                                                    type="text"
                                                    className="form-control mb-1"
                                                    id="name"
                                                    name="name"
                                                    placeholder="Enter Your Name"
                                                    value={this.state.name}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="text"
                                                    className="form-control mb-1"
                                                    id="email"
                                                    name="email"
                                                    placeholder="Enter Your Email"
                                                    value={this.state.email}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="password"
                                                    className="form-control mb-1"
                                                    id="password"
                                                    name="password"
                                                    placeholder="Password"
                                                    value={this.state.password}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                        </>
                                    ) : (
                                        <>
                                            <div className="form-group">
                                                <input
                                                    type="text"
                                                    className="form-control mb-1"
                                                    id="email"
                                                    name="email"
                                                    placeholder="Enter Your Email"
                                                    value={this.state.email}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="password"
                                                    className="form-control mb-1"
                                                    id="password"
                                                    name="password"
                                                    placeholder="Password"
                                                    value={this.state.password}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                        </>
                                    )}

                                    <div className="text-end">
                                        <button
                                            type="submit"
                                            className="btn btn-primary"
                                        >
                                            {this.state.mode === "signIn"
                                                ? "Sign in"
                                                : "Sign Up"}
                                        </button>
                                    </div>
                                </form>
                            </div>
                            <div className="card-footer">
                                <p
                                    className="text-center"
                                    onClick={this.modeHandler}
                                >
                                    {this.state.mode === "signIn"
                                        ? "Not registered?"
                                        : "Already have an account?"}{" "}
                                    <span className="text-primary">
                                        {this.state.mode === "signIn"
                                            ? "Create an account"
                                            : "Sign in"}
                                    </span>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}
