import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPen } from "@fortawesome/free-solid-svg-icons";
import { userInfo } from "../../API/auth";
import {
    getCategory,
    createCategory,
    deleteCategory,
    getRole,
    createRole,
    deleteRole,
} from "../../API/utilities";

export default function CategoryAndRole(props) {
    const [value, setValue] = useState("");
    const [roleValue, setRoleValue] = useState("");
    const [category, setCategory] = useState([]);
    const [role, setRole] = useState([]);
    const [message, setMessage] = useState(null);

    const formHandler = (e) => {
        if (e.target.name === "category") setValue(e.target.value);

        if (e.target.name === "role") setRoleValue(e.target.value);
    };

    const submitHandler = (e) => {
        e.preventDefault();
        if (e.target.id === "category") {
            createCategory({ name: value }, userInfo().token)
                .then((result) => {
                    setValue("");
                    getCategory(userInfo().token)
                        .then((result) => setCategory(result.data))
                        .catch((err) => console.log(err));
                })
                .catch((err) => {
                    setMessage(err.response.data);
                });
        }

        if (e.target.id === "role") {
            createRole({ name: roleValue }, userInfo().token)
                .then((result) => {
                    setRoleValue("");
                    getRole(userInfo().token)
                        .then((result) => setRole(result.data))
                        .catch((err) => console.log(err));
                })
                .catch((err) => {
                    setMessage(err.response.data);
                });
        }
    };

    const deleteHandler = (id, type) => {
        if (type === "category") {
            deleteCategory(id, userInfo().token)
                .then((result) => {
                    setMessage("successfully deleted!");
                    getCategory(userInfo().token)
                        .then((result) => setCategory(result.data))
                        .catch((err) => console.log(err));
                })
                .catch((err) => console.log(err));
        }

        if (type === "role") {
            deleteRole(id, userInfo().token)
                .then((result) => {
                    setMessage("successfully deleted!");
                    getRole(userInfo().token)
                        .then((result) => setRole(result.data))
                        .catch((err) => console.log(err));
                })
                .catch((err) => console.log(err));
        }
    };

    useEffect(() => {
        getCategory(userInfo().token)
            .then((result) => setCategory(result.data))
            .catch((err) => console.log(err));

        getRole(userInfo().token)
            .then((result) => setRole(result.data))
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title="Category and Roles">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Category and Roles</h1>
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <section>
                            <form id="category" onSubmit={submitHandler}>
                                <div className="row">
                                    <h1>Category</h1>
                                    <div className="col">
                                        <div className="d-flex">
                                            <FontAwesomeIcon
                                                className="m-3"
                                                icon={faPen}
                                            />
                                            <input
                                                name="category"
                                                type="text"
                                                className="form-control"
                                                value={value}
                                                onChange={formHandler}
                                            />
                                        </div>
                                    </div>
                                    <div className="col">
                                        <button
                                            type="submit"
                                            className="btn btn-md btn-primary"
                                        >
                                            Submit
                                        </button>
                                    </div>
                                </div>
                            </form>
                            <ul className="list-group">
                                {category.map((item) => (
                                    <li
                                        key={item._id}
                                        className="list-group-item d-flex justify-content-between align-items-center my-1 text-capitalize"
                                    >
                                        {item.name}
                                        <button
                                            type="button"
                                            className="btn btn-danger btn-sm"
                                            onClick={() =>
                                                deleteHandler(
                                                    item._id,
                                                    "category"
                                                )
                                            }
                                        >
                                            <FontAwesomeIcon icon={faTrash} />
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        </section>
                        <section>
                            <form id="role" onSubmit={submitHandler}>
                                <div className="row">
                                    <h1>Roles</h1>
                                    <div className="col">
                                        <div className="d-flex">
                                            <FontAwesomeIcon
                                                className="m-3"
                                                icon={faPen}
                                            />
                                            <input
                                                name="role"
                                                type="text"
                                                className="form-control"
                                                value={roleValue}
                                                onChange={formHandler}
                                            />
                                        </div>
                                    </div>
                                    <div className="col">
                                        <button
                                            type="submit"
                                            className="btn btn-md btn-primary"
                                        >
                                            Submit
                                        </button>
                                    </div>
                                </div>
                            </form>
                            <ul className="list-group">
                                {role.map((item) => (
                                    <li
                                        key={item._id}
                                        className="list-group-item d-flex justify-content-between align-items-center my-1 text-capitalize"
                                    >
                                        {item.name}
                                        <button
                                            type="button"
                                            className="btn btn-danger btn-sm"
                                            onClick={() =>
                                                deleteHandler(item._id, "role")
                                            }
                                        >
                                            <FontAwesomeIcon icon={faTrash} />
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        </section>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
